
import { activeFicha as ficha } from "@/entities/ficha";
import Ficha from "@/entities/ficha/model/Ficha";
import { defineComponent } from "vue";

export default defineComponent({
  name: "PontosModal",
  props: {},
  data() {
    return {
      pontos: 10 as number,
    };
  },
  computed: {
    totalCusto(): number {
      return ficha.modificadores.reduce(
        (total, mod) => total + this.calcularCusto(mod.base),
        0
      );
    },
    ficha(): Ficha {
      return ficha;
    },
  },
  methods: {
    updateTotalCusto(): void {
      this.pontos = 10 - this.totalCusto;
      console.log("totalcusto", this.totalCusto);
      console.log("pontos", this.pontos);
    },
    calcularCusto(valor: number): number {
      let custo: number;

      switch (valor) {
        case -1:
          custo = -1;
          break;
        case 0:
          custo = 0;
          break;
        case 1:
          custo = 1;
          break;
        case 2:
          custo = 2;
          break;
        case 3:
          custo = 4;
          break;
        case 4:
          custo = 7;
          break;
        default:
          custo = 0;
          break;
      }

      return custo;
    },
  },
  mounted() {
    console.log("totalcusto", this.totalCusto);
    console.log("pontos", this.pontos);
  },
});
